.about-project {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.about-project__container {
    max-width: 1141px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 0 110px 0;
    transition: 1s ease-in-out;
}

.about-project__text-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 70px auto 110px;
    transition: 1s ease-in-out;
}

.about-project__text-block {
    width: 550px;
    margin: 0 40px 0 0;
    transition: 1s ease-in-out;
}

.about-project__text-block:last-of-type {
    margin: 0;
}

.about-project__subtitle{
    font-size: 1.25rem;
    font-weight: 300;
    letter-spacing: -4%;
    text-align: left;
    color: #000;
    margin: 0 auto 26px;
}

.about-project__text {
    max-width: 450px;
    font-size: .7rem;
    font-weight: 300;
    text-align: left;
    color: #000;
    margin: 0;
    transition: 1s ease-in-out;
}

.about-project__line-container {
    width: 100%;
    display: grid;
    grid-template-columns: 20% 1fr;
    column-gap: 0;
    margin: 0;
}

.about-project__line {
    min-height: 36px;
    font-size: .7rem;
    line-height: 0;
    font-weight: 300;
    text-align: center;
    color: #000;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0;
}

.about-project__color-line {
    width: 100%;
    background-color: #2BE080;
}

.about-project__grey-line {
    width: 100%;
    background-color: #F2F2F2;
}

.about-project__line-text {
    font-size: .68rem;
    line-height: 1.1rem;
    font-weight: 300;
    text-align: center;
    color: #A0A0A0;
    margin-block-end: 0;
}


@media screen and (max-width: 1141px) {
    .about-project__container {
        max-width: 86%;
    }
}


@media screen and (max-width: 767px) {
    .about-project__container {
        max-width: 88%;
        margin: 0 0 70px;
    }

    .about-project__text-container {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 60px auto 60px;
    }

    .about-project__text-block {
        max-width: 100%;
        margin: 0 0 56px;
    }

    .about-project__text-block:last-of-type {
        margin: 0;
    }

    .about-project__text {
        max-width: 100%;
    }

    .about-project__line-container {
        grid-template-columns: 35% 1fr;
    }

    .about-project__line {
        font-size: .68rem;
    }
    
}